import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { InfoNickname } from ".";

const CommentStyle = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f8f8f8;
  height: 100px;
  border-radius: 6px;
  margin: 10px 20px;
`;
const InfoExplanationTitle = styled.div`
  font-size: 24px;
  font-weight: bold;
  margin: 40px 20px;
  border-top: 1px solid rgb(238, 238, 238);
  padding-top: 20px;
`;

const Comment = styled.div`
  font-size: 18px;
  margin: 10px;
  font-weight: 600;
`;

const CommentDate = styled.div`
  font-size: 14px;
  margin: 10px;
  font-weight: 400;
  color: #888888;
`;

const CommentBox = styled.div`
  box-shadow: 1px 2px 16px rgba(0, 0, 0, 0.16);
  margin: 0px 20px 20px 20px;
  border-radius: 8px;
`;

const CommentHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const CommentInput = styled.input`
  border: none;
  outline: none;
  background-color: white;
  font-size: 18px;
  font-weight: 700;
  margin-left: 10px;
`;

export default function BoardComment({ boardname }) {
  const [comments, SetComments] = useState([]);
  const { id } = useParams();
  const [editNum, setEditNum] = useState("");
  const [comment, setComment] = useState("");
  const config = {
    headers: {
      "content-type": "application/json",
    },
  };
  useEffect(() => {
    const result = async () => {
      const data = await axios(`/api/post/${id}/comments`);
      SetComments(data.data.data);
    };
    result();
  }, [id]);
  const onDelete = (item) => {
    if (window.confirm("삭제하시겠습니까?") === true) {
      axios.post(`/api/comment/${item}/delete`).then((res) => {
        res.status === 200 && window.location.replace(`/${boardname}/${id}`);
      });
    }
  };
  const onEdit = (item, itemcomment) => {
    setComment(itemcomment);
    setEditNum(item);
  };

  const onChangeEdit = (e) => {
    e.preventDefault();
    setComment(e.target.value);
  };

  const changeSubmit = (item) => {
    const data = {
      commentId: item,
      comment,
    };
    console.log(data);
    axios.post(`/api/comment/${item}/edit`, data, config);
  };
  return (
    <>
      <InfoExplanationTitle>{`댓글 ${comments.length}`}</InfoExplanationTitle>
      {comments.map((x) => (
        <CommentBox key={x.id}>
          <CommentHeader>
            <InfoNickname>{x.nickName}</InfoNickname>
            <div style={{ display: "flex" }}>
              <div
                style={{ margin: "10px", fontSize: "24px", color: "red" }}
                onClick={() => onDelete(x.id, x.comment)}
              >
                x
              </div>
              <div
                style={{ margin: "10px", fontSize: "24px", color: "red" }}
                onClick={() => onEdit(x.id, x.comment)}
              >
                e
              </div>
            </div>
          </CommentHeader>
          {editNum === x.id ? (
            <CommentStyle>
              <form onSubmit={changeSubmit(x.id)}>
                <CommentInput
                  style={{ width: "90%" }}
                  type="text"
                  value={comment}
                  onChange={onChangeEdit}
                />
                <CommentInput type="submit" value="수정하기" />
              </form>
            </CommentStyle>
          ) : (
            <CommentStyle>
              <Comment>{x.comment}</Comment>
              <CommentDate>{x.createdDate}</CommentDate>
            </CommentStyle>
          )}
        </CommentBox>
      ))}
    </>
  );
}
